import React from 'react'
import "./Countries.css"
import { Link } from 'react-router-dom'
import { useDestructure } from './ContextProvider';


const COUNTRY = () => {
    const { data } = useDestructure();
    return (

        <div className='country'>



            <div className='world'>
                {data.map((value) => {
                    return <div className='pto'>
                        <h2>{value.countryCode}</h2>
                        <p>{value.city}</p>
                    </div>
                })}


            </div>



        </div>

    )
}

export default COUNTRY