import React, { createContext, useContext, useReducer, useState } from 'react'
const World = createContext();

const ContextProvider = ({ children }) => {
    const reducer = (state, action) => {
        switch (action.type) {
            case "map":
                return { ...state, map: action.payload };
            case "data":
                return { ...state, data: action.payload };
            case "city":
                return { ...state, city: action.payload };
            case "date":
                return { ...state, calender: action.payload };
            case "notes":
                return { ...state, notes: action.payload };

            default:
                return { ...state };
        }

    }

    const arr = [
        {
            id: 1,
            country: "Portugal",
            countryCode: "PT",
            city: "Lisbon",
            date: new Date().toDateString(),
            note: "Portugal is my favorite city so far",
            lat: 98.68,
            lng: 6.87,
        },
        {
            id: 2,
            country: "Spain",
            countryCode: "ES",
            city: "Madrid",
            date: new Date().toDateString(),
            note: "Madrid is my favorite city so far",
            lat: 68.68,
            lng: 76.87,
        },
        {
            id: 3,
            country: "France",
            countryCode: "FR",
            city: "Paris",
            date: new Date().toDateString(),
            note: "France is my favorite city so far",
            lat: 18.68,
            lng: -26.87,
        },

    ]
    const initialize = { map: false, data: [...arr], city: "", calender: "", notes: "" }
    const [state, dispatch,] = useReducer(reducer, initialize);

    const { map, data, city, date, notes } = state;






    // const [map, setMap] = useState(false);
    // const [data, setData] = useState([...arr]);
    // const [city, setCity] = useState("");
    // const [date, setDate] = useState("");
    // const [notes, setNotes] = useState("");

    const addData = () => {
        const newObj = {
            id: Date.now(),
            country: "france",
            countryCode: "TR",
            city,
            date,
            notes,
            lat: 18.68,
            lng: -26.87,
        }


        handleArr(newObj);
        dispatch({ type: "map", payload: false })

        // setMap(false);
    }


    const handleArr = (newObj) => {
        // setData((data) => [...data, newObj])
        dispatch({ type: "data", payload: [...data, newObj] })

    }

    const handleMap = () => {
        // setMap(true);
        dispatch({ type: "map", payload: true })

    }
    const handlecity = () => {
        // setMap(false);
        dispatch({ type: "map", payload: false })
    }
    return (
        <div>
            <World.Provider value={{ data, handleMap, handlecity, dispatch, map, addData, city, date, notes, }}>
                {children}
            </World.Provider>

        </div>
    )
}

const useDestructure = () => {
    const updateFile = useContext(World);
    return updateFile

}


export { ContextProvider, useDestructure }