import React from 'react'
import "./AddForm.css"
import { useDestructure } from './ContextProvider'
import { Link } from 'react-router-dom'


const AddForm = () => {
    const { addData, city, date, notes, dispatch } = useDestructure();

    return (
        <div>
            <form >
                <div className='formContainer'>
                    <div className='formInput'>
                        <label for="city">City name</label>
                        <input type="text" id="city" value={city} onChange={(e) => dispatch({ type: "city", payload: (e.target.value) })}></input>
                    </div>
                    <div className='formInput'>
                        <label for="calender">When did you go to ?</label>
                        <input type="date" id="calender" value={date} onChange={(e) => dispatch({ type: "date", payload: (new Date(e.target.value).toDateString()) })} ></input>
                    </div>
                    <div className='formInput'>
                        <label for="notes">Notes about your trip to</label>
                        <textarea id="notes" rows="4" cols="50" value={notes} onChange={(e) => dispatch({ type: "notes", payload: (e.target.value) })} ></textarea>
                    </div>
                    <div className='btns'>
                        <button type="button" onClick={addData} className='addbtn'>Add</button>
                        <Link to="Wise/Cities"> <button type="button" className="backbtn " >Back</button></Link>
                    </div>
                </div>
            </form >

        </div >
    )
}

export default AddForm