import React, { useState } from 'react'
import "./Wise.css"
import Map from './Map'
import AddForm from './AddForm'
import { Link, Outlet } from 'react-router-dom'
import { useDestructure } from './ContextProvider'


const Wise = () => {
    const { map, handleMap, handlecity } = useDestructure();
 
    return (
        <div className='wise'>
            <div className='citiwise'>
                <div className='top'>

                    <div className='logos'>
                        <Link to="/Home" ><img src="/image/logo.png"></img></Link>

                    </div>
                    <div className='btnsList'>
                        <ul>
                            <li><Link to="Cities" onClick={handlecity}>CITIES</Link></li>
                            <li><Link to="Countries">COUNTRY</Link></li>
                        </ul>

                    </div>
                    <div className='country_list'>
                        {map ? <AddForm /> : <Outlet />}
                    </div>
                </div>
                <div className='footer'>
                    <p> Copyright 2023 by WorldWise Inc.</p>
                </div>


            </div>

            <div className='map'>

                <Map handleMap={handleMap} />
            </div>
        </div>
    )
}

export default Wise