import React from 'react'
import { Link } from 'react-router-dom'
import { useDestructure } from './ContextProvider'
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { lime, purple } from '@mui/material/colors';
import Button from '@mui/material/Button';

const CITIES = () => {

    const theme = createTheme({
        palette: {
            primary: lime,
            secondary: purple,
        },
    });

    const { data, dispatch } = useDestructure();
    const deleted = (id) => {


        let res = data.filter((value) => value.id != id)
        dispatch({ type: "data", payload: res })

    }
    return (
        <div>
            <ul>
                {data.map((value) => {
                    return (
                        <div className='delBtn'><Link to={`PT/${value.id}`}> <li>
                            <span className='pt'>{value.countryCode}</span>
                            <h3 className='lisbon'>{value.city}</h3>
                            <p>{value.date}</p>

                        </li>
                        </Link>
                            <ThemeProvider theme={theme}>
                                <Button variant="contained" onClick={() => deleted(value.id)}>X</Button>
                                {/* <Button variant="contained" color="secondary" sx={{ ml: 2 }}>
                                    Secondary
                                </Button> */}
                            </ThemeProvider>

                            {/* <button type="button" className='closebtn' onClick={() => deleted(value.id)}>X</button> */}

                        </div>)
                })}

            </ul>





        </div>
    )
}

export default CITIES