import React from 'react'
import { useDestructure } from './ContextProvider'
import "./PT.css"

import { Link, useParams } from 'react-router-dom'

const PT = () => {
    const { data } = useDestructure();
    // console.log(data);
    const { id } = useParams()
    // console.log(id);
    const newData = data.find((e) => e.id == id)
    // console.log(newData);
    return (

        <div className='pts'>
            {/* {data.map((value) => { */}
            <div className='pt_con'>
                <div className='con'>
                    <h6 className='color'>CITY NAME</h6>
                    <h3><span>{newData.countryCode}</span>{newData.city}</h3>
                </div>
                <div className='date'>
                    <h6 className='color'>YOU WENT TO {newData.city} ON</h6>
                    <p className='sun'>{newData.date}</p>
                </div>
                <div className='note'>
                    <h6 className='color'>YOUR NOTES</h6>
                    <p className='sun'>{newData.note}</p>
                </div>
                <div className='more'>
                    <h6 className='color'>LEARN MORE</h6>
                    <a href="#">Checkout Lisbon on Wikipediea</a>
                </div>
                <div className='backbtn'>
                    <Link to="/Wise/Cities"> <button type="button">Back</button></Link>

                </div>

            </div>

        </div>


    )
}

export default PT