import React from 'react'
import { Link, useNavigate } from 'react-router-dom'

const Demo = () => {
    const navigate = useNavigate()
    let arr = [{ id: 1, name: "Raj" }
        , { id: 2, name: "Bheem" },
    { id: 3, name: "gowsig" }]
    return (
        <div>
            {
                arr.map((e) => {
                    return (
                        <>
                            <Link to={`/Price/${e.name}/${e.id}`}>
                                {/* <div onClick={() => navigate(`/Price/${e.name}/${e.id}`)}>{e.name}</div> */}
                                <div>{e.name}</div>

                            </Link>

                        </>
                    )
                })
            }
        </div>
    )
}

export default Demo


