import React, { useState, useEffect } from 'react'
import "./Login.css"
import Navbar from './Navbar'
import { Link, Outlet, useNavigate } from 'react-router-dom'
import { useForm } from 'react-hook-form'
import { hasFormSubmit } from '@testing-library/user-event/dist/utils'

const Login = () => {

  const navigate = useNavigate();

  const { register, handleSubmit, formState: { errors } } = useForm();







  const formData = [
    {
      id: 1,
      email: "gowsig@gmail.com",
      password: "QWss@123"
    },
    {
      id: 2,
      email: "raj@gmail.com",
      password: "QWss@456"
    },
    {
      id: 3,
      email: "gowsigaraj@gmail.com",
      password: "QWss@789"
    }


  ]

  const submits = (items) => {
    console.log(items);
    let dd = formData.find((e) => e.email == items.email);
    console.log(dd);
    if (dd == undefined) {
      console.log("login failiur");
    }
    else {
      if (items.password == dd.password) {

        console.log("login succss");
        navigate("/Wise ")



      }
      else {
        console.log("login check email or password");
      }
    }





  }




  return (


    <>
      <Navbar />
      <div className='login'>
        {/* <pre>{JSON.stringify(formData, undefined,)}</pre> */}
        <form action="" id="form" onSubmit={handleSubmit(submits)}>
          <div className='form_input'>
            <label for="email">Email address</label>
            <input type="text" name="email" id="email" placeholder='gowsig@gmail.com'  {...register("email", {
              required: "email is empty", pattern: {
                value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
                message: "invalid email address"
              }
            })} ></input>
            <p style={{ color: "red" }}>{errors.email?.message}</p>
            {/* <p style={{ color: "red" }}>{errors.email?.message}</p> */}
          </div>

          <div className='form_input'>
            <label for="password">Password</label>
            <input type="text" name="password" id="password" placeholder='......' {...register("password", {
              required: "password is Required",
              maxLength: {
                value: 8,
                message: "must be max 8 chars",
              },
              validate: (value) => {
                return (
                  [/[a-z]/, /[A-Z]/, /[0-9]/].every((pattern) =>
                    pattern.test(value)
                  ) ||
                  "cannot special chars, only lower, upper, number"
                );
              },
            })}></input>
            <p style={{ color: "red" }}>{errors.password?.message}</p>

          </div>
          <div >
            <ul>
              {/* <Link to="/Wise"> */}
              <li><button type="submit" className='loginbtn' >LOGIN</button></li>
              {/* </Link> */}
            </ul>
            <Outlet />

          </div>



        </form >

      </div >
    </>

  )
}

export default Login